@use "../_all" as *;

ul, ol {
  padding-left: space("normal", "rem");
  line-height: line-height("normal");

  &:not(:last-child) {
    margin: 0 0 space("normal", "rem");
  }
}

ul[class]:not(.ul),
ol[class]:not(.ol) {
  list-style: none;
}

dl {
  line-height: line-height("normal");

  &:not(:last-child) {
    margin: 0 0 space("normal", "rem");
  }

  div {
    &:not(:last-child) {
      margin-bottom: space("normal", "rem");
    }
  }
}

dt {
  font-weight: font-weight("bold");
}